@import url(https://fonts.googleapis.com/css?family=Roboto);
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  height: 100%;

  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5em;

  color: rgba(0, 0, 0, 0.87);
  background: #f5f5f5;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

img {
  display: block;
  width: 100%;
}

ul {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;

  list-style-type: none;
}

#root {
  position: fixed;
  top: 50%;
  left: 50%;

  display: flex;
  align-items: flex-start;

  transform: translate(-50%, -50%);

  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
}


.List-container {
  position: relative;

  width: 18rem;

  background-color: #ffffff;
  border: 1px solid #dfdfdf;
  border-radius: 5px;
}

.List__item {
  padding: 10px 20px;
}

.List__item:not(:last-child) {
  border-bottom: 1px solid #dfdfdf;
}

.List__item:hover {
  background-color: #0f7ffa;
}

.List__item:hover .List-item__link {
  color: #ffffff;
}

.List.disabled li.List__item:hover {
  background-color: #ffffff;
}

.List.disabled li.List__item:hover .List-item__link {
  color: #313d51;
}

.List-item__link {
  display: block;
  padding: 10px 20px;
  margin: -10px -20px;

  color: #313d51;
}

.List.disabled .List-item__link {
  cursor: auto;
}

.Loading {
  position: absolute;
  top: 50%;
  left: 50%;

  width: 200px;
  height: 200px;

  transform: translateX(-50%) translateY(-50%);

  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
}

.Details {
  width: 18rem;
  padding: 24px;
  margin-left: 30px;
  overflow: hidden;

  color: #313d51;
  background-color: #ffffff;
  border: 1px solid #dfdfdf;
  border-radius: 5px;
}

.Details-title {
  margin-top: 0;
  margin-bottom: 0.75rem;

  font-weight: 400;
  font-size: 1.25rem;
}

.DetailsImg-container {
  height: 300px;
  margin: -24px -24px 30px -24px;

  background-color: #858d94;
}

.DetailsImg-content {
  position: relative;
  top: 50%;

  display: block;
  width: 100%;
  max-height: 300px;

  text-align: center;

  color: #ffffff;

  transform: translateY(-50%);
}

.DetailsList__item {
  padding: 10px 24px;
  margin-right: -24px;
  margin-left: -24px;
}

.DetailsList__item:not(:last-child) {
  border-bottom: 1px solid #dfdfdf;
}

